table {
    width: 100%;
    padding: 8px;
    text-align: left;
    border-spacing: 0px
}

th {
    padding: 8px;
    width: 20%;
}

td {
    padding: 8px;
}

.pd-cell {
    text-align: center;
    font-weight: bolder;
}

.pd-cell-red {
    background-color: #EE3123;
}

.pd-cell-green {
    background-color: #008000;
}


.pd-cell-amber {
    background-color: #FFA500;
}