.main-map {
    height: 100vh;
    width: 100%;
}

.marker-wrapper {
    cursor: pointer;
}

.marker-icon {
    position: "absolute";
    top: "100%";
    left: "50%";
    transform: "translate(-50%, -100%)";
}

.marker-group {
    display: flex;
    width: 55px;
    background-color: transparent;

}

.marker-counter {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 16px;
    height: 16px;
    padding: 8px;
    margin-left: -8px;
    text-align: center;
    font-size: 14px;
    color: #fff;
    border-radius: 50%;
    background-color: #8D8F9B;
}