.simple-map-sidebar {
    font-family: "Arial" !important;
    text-align: left;
    overflow-x: hidden;
    overflow-y: scroll;
    height: calc(100vh)
}

.text {
    padding-left: 8px;
    white-space: pre-line;
    margin-top: 5px;
    margin-bottom: 5px;
}

.id {
    color: #EE3123;
    font-weight: bolder;
    font-size: medium;
}

.header {
    font-weight: bolder;
    font-size: medium;
}

button {
    vertical-align: top;
    margin: 8px;
    height: 85px;
    overflow: hidden;
    background-color: #FFFFFF;
    border-radius: 16px;
    border-color: #8D8F9B;
    vertical-align: middle;
}

button div {
    word-wrap: break-word;
    width: 100px
}